.actionsBackdrop,
.actionsBackdropOpen {
  position: absolute;
  top: 0;
  left: 0;

  overflow: hidden;

  width: 100%;
  height: 100%;

  opacity: 0;
  pointer-events: none;
  transform: scale(0);
  transition: all var(--timing-function__main);
}
.actionsBackdropOpen {
  z-index: 5;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: scale(1);
  background-color: rgba(20, 20, 20, 0.3);
  backdrop-filter: blur(2px);
}

.actionsContainer {
  position: absolute;
  top: 50%;
  left: 50%;

  text-align: center;
  padding: 16px;
  max-height: 80%;
  width: 95%;

  border-radius: 4px;
  background-color: rgba(254, 254, 254, 0.7);
  box-shadow: 0 0 8px rgba(122, 122, 122, 0.8);
  transform: translate(-50%, -50%);
  transition: all var(--timing-function__main);
}
.actionsList {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 8px;

  overflow: visible;
}
.listItem {
  position: relative;

  width: 36px;
  height: 36px;
  &:hover {
    & .actionTitle {
      opacity: 1;
      pointer-events: all;
      visibility: visible;
    }
  }
}

.actionActive,
.actionNotActive {
  display: flex;
  flex-direction: column;

  position: relative;

  fill: #010101;
  border-radius: 4px;
  font-size: 12px;
  color: #010101;

  gap: 4px;
  width: 36px;
  height: 36px;

  cursor: pointer;

  &:hover {
    background-color: rgba(122, 122, 122, 0.3);
    fill: var(--brand_clr__1);
    color: var(--brand_clr__1);
  }
}
.actionNotActive {
  opacity: 0.5;
  pointer-events: none;
}
.actionTitle {
  position: absolute;
  top: 100%;
  left: 50%;

  padding: 4px;

  min-width: 100%;

  font-size: 10px;

  transform: translateX(-50%);

  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.2);

  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.hover {
  // display: grid;
  // grid-template-columns: min-content 1fr;
  fill: #010101;
  border-radius: 4px;
  font-size: 12px;
  color: #010101;
  &:hover {
    background-color: rgba(122, 122, 122, 0.3);
    fill: var(--brand_clr__1);
    color: var(--brand_clr__1);
  }
}
